.radio {
  position: absolute;
  z-index: -1;
  opacity: 0;
}
.radio + label {
  position: relative;
  cursor: pointer;
}
.radio + label:before {
  content: '';
  position: absolute;
  border-radius: 50%;
  background: #FFF;
}

.Panel--ios .radio + label:before {
  width: 20px;
