  border: 2px solid #AEBFCF;
}

.radio + label:after {
  content: '';
  position: absolute;
  border-radius: 50%;
  opacity: 0;
  -webkit-transition: .2s;
  transition: .2s;
}

.Panel--ios .radio + label:after {
